import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import ahoaraLegalLogo from '../assets/ahoraLegalLogo.svg';
import successStories from '../successStories';
import estrella from '../assets/estrella.svg';

const useStyles = makeStyles((theme) => ({
    root: {
        paddingLeft: 30,
        position: 'relative',
        width: '100%',
        marginBottom: 100,
    },
    arrowRight: {
        position: 'absolute',
        right: 0,
        bottom: 350,
        fontSize: '3rem',
        zIndex: 10,
        cursor: 'pointer',
        userSelect: 'none',
    },
    arrowLeft: {
        position: 'absolute',
        left: 0,
        bottom: 350,
        fontSize: '3rem',
        zIndex: 10,
        cursor: 'pointer',
        userSelect: 'none',
    },
    title: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: 100,
    },
    logo: {
        width: 30,
        marginRight: 10,
    },
    card: {
        maxWidth: 300,
        height: 370,
    },
  }));

const SuccessStories: React.FC = () => {
    const classes = useStyles();

    return(
        <>
        <div id="stories" className={classes.title}>
            <img
                className={classes.logo}
                src={ahoaraLegalLogo}
                alt="Ahora legal logo rojo"
            />
            <Typography variant="h4" noWrap>
                <strong>Consultas Resueltas</strong>
            </Typography>
        </div>
        <Grid container wrap="nowrap" justify="space-around" style={{ boxShadow: '0px 15px 5px #e2dede', paddingBottom: 200, paddingTop: 200 }}>
        {successStories.map((slide, index) => {
                    return(
                        <Grid key={index}>
                             <Card className={classes.card}>
                                <CardContent>
                                    <Grid container justify="center" style={{ paddingBottom: 10 }}>
                                    <img src={estrella} alt="estrella" style={{ paddingRight: 10 }} />
                                    <img src={estrella} alt="estrella" style={{ paddingRight: 10 }} />
                                    <img src={estrella} alt="estrella" />
                                    </Grid>
                                    <Typography style={{ paddingBottom: 10 }}><strong>{slide.title}</strong></Typography>
                                    <Typography style={{ paddingBottom: 10 }}>{slide.subtitle}</Typography>
                                    <Typography><strong>{slide.hashtag}</strong></Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    )})
                }
        </Grid>
    </>
    )
};

export default SuccessStories;
