import { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import logo from '../assetsMobile/frontImg-min.png';
import familia from '../assetsMobile/familia-min.png';
import penal from '../assetsMobile/penal-min.png';
import comercio from '../assetsMobile/comercio-min.png';
import consumidor from '../assetsMobile/consumidor-min.png';

const useStyles = makeStyles((theme) => ({
    background: {
        width: '100%',
        height: 800,
    },
    slide: {
        position: 'absolute',
        opacity: 0,
    },
    slideActive: {
        position: 'absolute',
        top: 0,
        right: 0,
        left: 0,
        opacity: 1,
    },
}));

const assets = [
    {
        src: logo,
        alt: 'logo',
    },
    {
        src: familia,
        alt: 'familia',
    },
    {
        src: penal,
        alt: 'penal',
    },
    {
        src: comercio,
        alt: 'comercio',
    },
    {
        src: consumidor,
        alt: 'consumidor',
    }
]

const HeroMobile = () => {
    const classes = useStyles();
    const [current, setCurrent] = useState(0);

    const nextImage = () => {
        if(current === assets.length -1){
            setCurrent(0)
        } else {
            setCurrent(current + 1)
        }
    };

    setTimeout(nextImage, 3000)

    return (
        <>
        <div className={classes.background}>
            {assets.map((image, index) => {
                return(
                    <div key={index} className={index === current ? classes.slideActive : classes.slide}>
                        <img src={image.src} alt={image.alt} style={{ width: '100%'}} />
                    </div>
                )
              })
            }
        </div>
        </>
    )
};

export default HeroMobile;